import { IToy } from '../../../../interface';

export default (nameSort: string) => {
  switch (nameSort) {
    case 'new-first':
      return (a: IToy, b: IToy) => b.year - a.year;
    case 'old-first':
      return (a: IToy, b: IToy) => a.year - b.year;
    case 'z-a':
      return (a: IToy, b: IToy) =>
        b.name.toLowerCase().localeCompare(a.name.toLowerCase());
    default:
      return (a: IToy, b: IToy) =>
        a.name.toLowerCase().localeCompare(b.name.toLowerCase());
  }
};
