import { IToy } from '../../../../interface';
import { TToyParameterKey } from '../../../../type';

interface IParamFilter {
  array: IToy[];
  valueName: TToyParameterKey;
  paramCollection: string[];
}

export default ({ array, valueName, paramCollection }: IParamFilter) =>
  paramCollection.length
    ? array.filter((toy) => paramCollection.includes(toy[valueName]))
    : array;
