import React, { FC, useContext } from 'react';

import gameStoreContext from '../../../../context';

import { IToy } from '../../../../interface';

import './style.scss';
import ToyCard from './toy-card';

interface IToysList {
  className: string;
  toys: IToy[] | undefined;
}

const ToysList: FC<IToysList> = ({ className, toys }) => {
  const { toggleFavorite } = useContext(gameStoreContext);

  const addOrRemoveFavoriteToy = (e: React.MouseEvent<HTMLElement>): void => {
    const toyNum = (e.target as HTMLElement).closest('.toy-list__item');
    if (toyNum instanceof HTMLElement) {
      toggleFavorite(Number(toyNum.dataset.num));
    }
  };

  return (
    <ul className={`${className} toy-list`} onClick={addOrRemoveFavoriteToy}>
      {toys?.length ? (
        toys?.map((toy) => (
          <ToyCard {...toy} className="toy-list__item" key={toy.num} />
        ))
      ) : (
        <p>Sorry, no matches found.</p>
      )}
    </ul>
  );
};

export default ToysList;
