import { FC, useState } from 'react';
import { IToy } from '../../../../../interface';

import './style.scss';

const ToyCard: FC<IToy & { className: string }> = ({
  num,
  name,
  count,
  year,
  shape,
  color,
  size,
  favorite,
  className,
}) => {
  const [imgSrc, setImgSrc] = useState<string>('');
  const img = new Image();
  img.src = `./assets/toys/${num}.png`;
  img.onload = () => {
    setImgSrc(img.src);
  };

  return (
    <li
      data-num={num}
      className={`${className} toy-card ${favorite ? 'toy-card_favorite' : ''}`}
    >
      <span>
        <span>
          <h3 className="toy-card__title">{name}</h3>
          {imgSrc ? (
            <img className="toy-card__img" src={imgSrc} alt={name} />
          ) : (
            <div className="toy-card__not-img"></div>
          )}
          <p>count: {count}</p>
          <p>year: {year}</p>
          <p>shape: {shape}</p>
          <p>color: {color}</p>
          <p>size: {size}</p>
        </span>
      </span>
    </li>
  );
};

export default ToyCard;
