import { FC } from 'react';
import InputRange from 'react-input-range';
import 'react-input-range/lib/css/index.css';

import './style.scss';

interface IRangeEvent {
  name: string;
  min: number;
  max: number;
}

interface IRange {
  title: string;
  name: string;
  max: number;
  min: number;
  curMax: number;
  curMin: number;
  onChange: (s: IRangeEvent) => void;
}

const Range: FC<IRange> = ({
  title,
  name,
  max,
  min,
  curMax,
  curMin,
  onChange,
}) => {
  return (
    <fieldset className="filter-form__item range">
      <legend className="filter-form__title range__title">{title}</legend>
      <InputRange
        name={name}
        draggableTrack
        maxValue={max}
        minValue={min}
        onChange={(value) => {
          const { min: minValue, max: maxValue } = value as {
            min: number;
            max: number;
          };
          onChange({ name, min: minValue, max: maxValue });
        }}
        value={{
          min: curMin,
          max: curMax,
        }}
      />
    </fieldset>
  );
};

export default Range;
