import { FC } from 'react';
import { NavLink } from 'react-router-dom';

import './style.scss';

const NavBar: FC = () => {
  type TLinkIsActiveProps = { isActive: boolean };

  const classesLink = ({ isActive }: TLinkIsActiveProps) =>
    isActive ? 'nav-bar__link nav-bar__link_active' : 'nav-bar__link';

  return (
    <nav className="nav-bar">
      <NavLink className="nav-bar__link nav-bar__link_tree" to="/" title="Home">
        {''}
      </NavLink>
      <NavLink className={classesLink} to="/toys">
        Toys
      </NavLink>
      <NavLink className={classesLink} to="/tree">
        Tree
      </NavLink>
    </nav>
  );
};

export default NavBar;
