import { FC, useState, useContext } from 'react';

import { IFilterState } from '../../../interface';

import gameStoreContext from '../../../context';
import Filter from './filter';

import './style.scss';
import ToysList from './toys-list';
import useFilter from './filter-hook';
import { defaultFilter } from '../../../constants';

const Toys: FC = () => {
  const [filter, setFilter] = useState<IFilterState>(defaultFilter);
  const { gameStorage, toggleFavorite } = useContext(gameStoreContext);

  const filtredToys = useFilter(filter, gameStorage);

  return (
    <section className="toys-container">
      <Filter
        className="toys-container__filter"
        filterState={filter}
        setfilterState={setFilter}
      />
      <h1 className="toys-container__title">Toys</h1>
      <ToysList className="toys-container__list" toys={filtredToys} />
    </section>
  );
};

export default Toys;
