import React, { FC } from 'react';
import Color from './color';
import Shape from './shape';
import Size from './size';
import Range from './range/input';

import './style.scss';
import { IFilterS } from '../../../../../interface';
import { defaultFilter } from '../../../../../constants';

interface IRangeEvent {
  name: string;
  min: number;
  max: number;
}

type TParameterName = 'color' | 'size' | 'shape';

const FilterForm: FC<IFilterS> = ({ filterState, setfilterState }) => {
  const { year, count } = filterState;

  const changeForm = (e: React.FormEvent) => {
    const { name, dataset } = e.target as HTMLInputElement;

    if (name === 'reset') {
      setfilterState(defaultFilter);
      return;
    }

    if (name === 'favorite') {
      const status = e.target as HTMLInputElement;
      setfilterState({ ...filterState, favorite: status.checked });
      return;
    }

    const filterArr = filterState[name as TParameterName];

    const paramValue = dataset.value;
    const paramIndex = filterArr.findIndex((param) => param === paramValue);

    if (paramIndex !== -1) {
      filterArr.splice(paramIndex, 1);
    } else {
      filterArr.push(paramValue as string);
    }

    setfilterState({ ...filterState, [name]: filterArr });
  };

  const changeRange = ({ name, min, max }: IRangeEvent) => {
    setfilterState({ ...filterState, [name]: { min, max } });
  };

  return (
    <form className="filter-form" onChange={changeForm}>
      <Shape />
      <Range
        title="Count"
        name="count"
        max={12}
        min={0}
        curMax={count.max}
        curMin={count.min}
        onChange={changeRange}
      />
      <Range
        title="Year"
        name="year"
        max={2020}
        min={1940}
        curMax={year.max}
        curMin={year.min}
        onChange={changeRange}
      />
      <Color />
      <Size />
      <fieldset className="filter-form__item">
        <label className="filter-form__item filter-form__favorite">
          <input
            className="filter-form__favorite-inpute"
            type="checkbox"
            name="favorite"
          />
          <span className="filter-form__favorite-text">Favorite only</span>
        </label>
      </fieldset>
      <label>
        <input className="filter-form__input" type="checkbox" name="reset" />
        <span className="filter-form__reset-button">Reset filter</span>
      </label>
    </form>
  );
};

export default FilterForm;
