import { FC, useContext } from 'react';
import gameStoreContext from '../../../../context';

import './style.scss';

const TotalFavoriteToys: FC = () => {
  const { gameStorage } = useContext(gameStoreContext);
  const favoriteToys = gameStorage?.favorite?.length;

  return favoriteToys ? (
    <div className="total-favorite">
      You like {favoriteToys} toy{favoriteToys == 1 ? '' : 's'}
    </div>
  ) : (
    <></>
  );
};

export default TotalFavoriteToys;
