import { FC } from 'react';

import './style.scss';

import { sizes } from '../../../../../../constants';

const Size: FC = () => {
  return (
    <fieldset className="filter-form__item">
      <legend className="filter-form__title">Size</legend>
      <div className="size-container">
        {sizes.map(({ title, value }, shapeIndex) => (
          <span key={title}>
            <input
              type="checkbox"
              className="size__input"
              name="size"
              data-value={value}
              id={`size_${shapeIndex}`}
            />
            <label
              className="filter-form__size size"
              htmlFor={`size_${shapeIndex}`}
            >
              {title}
            </label>
          </span>
        ))}
      </div>
    </fieldset>
  );
};

export default Size;
