import { useMemo } from 'react';

import { IFilterState, IGameStorage } from '../../../../interface';

import sortToys from './sort-toys';
import rangeSort from './range-filter';
import parameterFilter from './filter-by-parameter';

const useFilter = (
  filter: IFilterState,
  gameStorage: IGameStorage | undefined,
) => {
  const filtredToys = useMemo(() => {
    let resultArrToys = gameStorage?.toys;
    const favoriteToys = gameStorage?.favorite;
    const sortFunction = sortToys(filter.sort);

    if (resultArrToys) {
      resultArrToys = resultArrToys?.filter((toy) =>
        toy.name.toLowerCase().includes(filter.search.toLowerCase()),
      );
      resultArrToys = resultArrToys.sort(sortFunction);

      resultArrToys = rangeSort({
        array: resultArrToys,
        valueName: 'count',
        min: filter.count.min,
        max: filter.count.max,
      });

      resultArrToys = rangeSort({
        array: resultArrToys,
        valueName: 'year',
        min: filter.year.min,
        max: filter.year.max,
      });

      resultArrToys = parameterFilter({
        array: resultArrToys,
        valueName: 'shape',
        paramCollection: filter.shape,
      });

      resultArrToys = parameterFilter({
        array: resultArrToys,
        valueName: 'color',
        paramCollection: filter.color,
      });

      resultArrToys = parameterFilter({
        array: resultArrToys,
        valueName: 'size',
        paramCollection: filter.size,
      });

      if (filter.favorite) {
        resultArrToys = resultArrToys.filter((toy) => toy.favorite);
      }
    }

    return resultArrToys;
  }, [filter, gameStorage]);

  return filtredToys;
};

export default useFilter;
