import { FC } from 'react';

import './style.scss';

import { colors } from '../../../../../../constants';

const Color: FC = () => {
  return (
    <fieldset className="filter-form__item">
      <legend className="filter-form__title">Color</legend>
      <div className="color-container">
        {colors.map(({ title, value, color }, colorIndex) => (
          <span key={title}>
            <input
              type="checkbox"
              className="color__input"
              name="color"
              data-value={value}
              id={`color_${colorIndex}`}
            />
            <label
              className="filter-form__color color"
              htmlFor={`color_${colorIndex}`}
              style={{ backgroundColor: color }}
            ></label>
          </span>
        ))}
      </div>
    </fieldset>
  );
};

export default Color;
