class AppStorage {
  protected storage: Storage;

  protected storageKey: string;

  constructor() {
    this.storage = window.localStorage;
    this.storageKey = 'CHRISTMAS_TREE_ALGOLJ';
  }

  get(): number[] {
    return (
      this.storage?.[this.storageKey]?.split(',').map((num: string) => +num) ||
      []
    );
  }

  set(keys: number[]): void {
    if (keys.join(',')) {
      this.storage[this.storageKey] = keys.join(',');
    } else {
      delete this.storage[this.storageKey];
    }
  }
}

export default AppStorage;
