import { FC } from 'react';

import './style.scss';

import { shape } from '../../../../../../constants';

const Shape: FC = () => (
  <fieldset className="filter-form__item">
    <legend className="filter-form__title">Shape</legend>
    <div className="shape-container">
      {shape.map(({ title, value, imgName }, shapeIndex) => (
        <label
          key={title}
          className="filter-form__shape shape"
          htmlFor={`shape_${shapeIndex}`}
        >
          <input
            type="checkbox"
            className="shape__input"
            name="shape"
            data-value={value}
            id={`shape_${shapeIndex}`}
          />
          <img
            className="shape__img"
            src={`./assets/svg/${imgName}.svg`}
            alt={title}
          />
          {title}
        </label>
      ))}
    </div>
  </fieldset>
);

export default Shape;
