import { IToy } from '../../../../interface';
import { TToyRangeKey } from '../../../../type';

interface IRangeFilter {
  array: IToy[];
  valueName: TToyRangeKey;
  min: number;
  max: number;
}

export default ({ array, valueName, min, max }: IRangeFilter) =>
  array.filter((toy) => toy[valueName] >= min && toy[valueName] <= max);
