import React, { FC } from 'react';

import './style.scss';

import { IFilterS } from '../../../../interface';
import { ChoiceSort } from '../../../../constants';
import { TSortValue } from '../../../../type';

import Select from './select';
import FilterForm from './form';

const Filter: FC<IFilterS> = ({ filterState, setfilterState, className }) => {
  const { search, sort } = filterState;

  const searchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setfilterState({ ...filterState, search: e.target.value });
  };

  const sortChange = (value: TSortValue) => {
    setfilterState({ ...filterState, sort: value });
  };

  return (
    <div className={`${className} filter`}>
      <input
        type="search"
        className="filter__search"
        placeholder="Search"
        autoComplete="off"
        value={search}
        onChange={searchChange}
      />
      <div>
        Sort:{' '}
        <Select
          className="filter__sort"
          selectValue={sort}
          onChange={sortChange}
          option={ChoiceSort}
        />
      </div>
      <FilterForm filterState={filterState} setfilterState={setfilterState} />
      <button className="filter__button">Reset settings</button>
    </div>
  );
};

export default Filter;
