import { FC, useState, useEffect } from 'react';
import { Route, Routes } from 'react-router-dom';

import Header from './header';

import { IGameStorage, IToy } from '../../interface';

import loadData from './load-data';
import AppStorage from './app-storage';

import gameStoreContext from '../../context';
import Toys from './toys';

const Main: FC = () => {
  const [gameStorage, setGameStorage] = useState<IGameStorage>();

  const storage = new AppStorage();

  const changeFavoriteToy = (num: number, toys: IToy[]) => {
    const newToys = toys;
    const toyIndex = newToys.findIndex((toy) => toy.num === num);
    newToys[toyIndex].favorite = !newToys[toyIndex].favorite;
    return newToys;
  };

  const firstLoad = async () => {
    let toys = await loadData();
    const favorite = await storage.get();
    favorite.forEach((num) => {
      toys = changeFavoriteToy(num, toys);
    });
    setGameStorage({
      toys,
      favorite,
    });
  };

  const toggleFavorite = (num: number) => {
    const favorite = gameStorage?.favorite;

    if (favorite) {
      const numIndex = favorite.findIndex(
        (favoriteItem) => favoriteItem === num,
      );

      if (numIndex === -1) {
        favorite.push(num);
      } else {
        favorite.splice(numIndex, 1);
      }
      const toys = changeFavoriteToy(num, gameStorage.toys);
      setGameStorage({ toys, favorite });
      storage.set(gameStorage.favorite);
    }
  };

  useEffect(() => {
    firstLoad();
  }, []);

  return (
    <gameStoreContext.Provider value={{ gameStorage, toggleFavorite }}>
      <Header />
      <Routes>
        <Route path="/toys" element={<Toys />} />
        <Route path="/tree" element={<div>Tree</div>} />
        <Route path="*" element={<div>Error</div>} />
      </Routes>
    </gameStoreContext.Provider>
  );
};

export default Main;
