import React, { FC } from 'react';
import { TSortValue } from '../../../../../type';

type TChoiceSort = {
  name: string;
  value: string;
};

interface ISelect {
  className?: string;
  selectValue: TSortValue;
  onChange: React.Dispatch<TSortValue>;
  option: TChoiceSort[];
}

const Select: FC<ISelect> = ({ className, selectValue, onChange, option }) => {
  const changeValue = (e: React.ChangeEvent<HTMLSelectElement>) => {
    onChange(e.target.value as TSortValue);
  };

  return (
    <select
      className={`${className}`}
      value={selectValue}
      onChange={changeValue}
    >
      {option.map(({ value, name }) => (
        <option className="filter__sort-option" key={value} value={value}>
          {name}
        </option>
      ))}
    </select>
  );
};

export default Select;
